const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			userInfo: {},
			imgUrl: '',
			imageUrl: '',
			token: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
		console.log(uni.getStorageSync('token'))
		this.token = uni.getStorageSync('token')
	},
	onShow() {
		this.getUserInfo()
	},
	methods: {
		// 进入页面
		goPageNavigateTo(e) {
			console.log(e)
			let url = e.currentTarget.dataset.name
			if(url == 'meNewbieRead'){
				uni.navigateTo({
					url: `/pages/articleDetail?id=${21}`
				})
				
			}else if(url == 'meSpread'){
				if(this.userInfo.type == 'normal'){
					uni.showToast({
						title: '您不是会员，暂无此权限',
						icon: 'none',
						duration: 1000
					})
					setTimeout(e =>{
						uni.navigateTo({
							url: `/pages/meVIP`
						})
					},1600)
				}else{
					uni.navigateTo({
						url: `/pages/meSpread`
					})
				}
			}else if(url == 'meMessage'){
				uni.navigateTo({
					url: `/pages/${url}?isType=${this.userInfo.news }`
				})
			} else{
				uni.navigateTo({
					url: `/pages/${url}`
				})
			}
		},
		getUserInfo() {
			// 用户信息获取
			uni.showLoading({
			  title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						this.userInfo = res.data
						this.imageUrl = this.imgUrl+this.userInfo.avatar
					}else{
						if(res.data.message == 'Unauthenticated.'){
							uni.showToast({
								title: '未检测到用户的登录记录，请进行登录',
								icon: 'none',
								duration: 3000
							});
							setTimeout(() => {
								// 进入登录页
								uni.reLaunch({
									url: './login'
								});
							}, 3000)
						}else{
							uni.showToast({
								title: res.data.message,
								icon: 'none',
								duration: 3000
							});
						}
					}
				}
			})
		}
	}
}
